/* Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&family=Ubuntu:wght@300;500;700&display=swap');

* {
  font-family: 'Poppins', sans-serif;
  /*font-family: 'Ubuntu', sans-serif;*/
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  scroll-padding-top: 2rem;
  scroll-behavior: smooth;
}

/* Some Variables */

:root {
  --main-color: #ff2c1f;
  --text-color: #020307;
}

body {
  background: var(--text-color);
  color: var(--bg-color);
}